<template>
    <div id="note">
      <div v-html="article.body" class="markdown-body" v-marked></div>
    </div>
</template>
<script>
import { getContent} from '@/api';
import { convertContent, b64ToUtf8 } from '@/utils/helper'

export default {
  data() {
    return {
      article:{},
      isProgress: false,
      repoName: 'note',
      fileName: 'README.md'
    }
  },
  created(){
    this.getNote()
  },
  methods: {
    /* eslint-disable */
    getNote: function(){
      this.isProgress = true
      getContent(this.repoName, this.fileName)
        .then(article => {
          // console.log(b64ToUtf8(article.content))
          this.article = convertContent(b64ToUtf8(article.content)) 
          this.isProgress = false
        })
    }
  }
};
</script>
<style scoped>
@import '../styles/app.css';
@import '../styles/base.css';
@import '../styles/atom-one-dark.css';
</style>


